@import '~xtend-library/src/core/typography/typography-setup.less';

/**
 * font face
 */

@import (css) url(https://fonts.googleapis.com/css?family=Open+Sans:400,600);

.font-sans--regular() {
  font-family: 'Open Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-weight: 400;
  font-style: normal;
}

.font-sans--semibold() {
  font-family: 'Open Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-weight: 600;
  font-style: normal;
}

.font-monospace() {
  font-family: monaco, Consolas, 'Lucida Console', monospace;
  font-weight: normal;
  font-style: normal;
}

/**
 * rem
 */

// rem

html {
  // base font size for rem
  // 100% is 16px
  // value(%) = desired(px) * 100 / 16
  font-size: 56.25%; // 9px
  @media @min-lg {
    font-size: 62.5%; // 10px
  }
}

body {
  .font-sans--regular();
  font-size: 1.5rem;
  color: @text;
  line-height: 1.8;
  font-variant-ligatures: none;
}

/**
 * spacing
 */

.spacing--none {
  .margin(top, none) !important;
  .margin(bottom, none) !important;
}

.spacing--small {
  .margin(top, medium) !important;
  .margin(bottom, small) !important;
  .margin(y, layout) !important;
}

.spacing--medium {
  .margin(top, big) !important;
  .margin(bottom, medium) !important;
  .margin(y, layout) !important;
}

.spacing--big {
  .margin(top, giant) !important;
  .margin(bottom, big) !important;
  .margin(y, layout) !important;
}

/**
 * header
 */

h1, .h1 {
  .spacing--medium();
  .font-sans--semibold();
  font-size: 4rem;
  line-height: 1;
  letter-spacing: 0;
  text-transform: none;
}

h2, .h2 {
  .spacing--medium();
  .font-sans--semibold();
  font-size: 3.5rem;
  line-height: 1.2;
  letter-spacing: 0;
  text-transform: none;
}

h3, .h3 {
  .spacing--medium();
  .font-sans--semibold();
  font-size: 2.5rem;
  line-height: 1.2;
  letter-spacing: 0;
  text-transform: none;
}

h4, .h4 {
  .spacing--medium();
  .font-sans--semibold();
  font-size: 2rem;
  line-height: 1.2;
  letter-spacing: 0;
  text-transform: none;
}

h5, .h5 {
  .spacing--medium();
  .font-sans--semibold();
  font-size: 1.7rem;
  line-height: 1.2;
  letter-spacing: 0;
  text-transform: none;
}

h6, .h6 {
  .spacing--medium();
  .font-sans--semibold();
  font-size: 1.3rem;
  line-height: 1.4;
  letter-spacing: .05em;
  text-transform: uppercase;
}

// special

.header--block {
  .spacing--medium();
  .padding(y, small);
  .padding(x, medium);
  background: @gray--200;
  text-align: center;
}

.header--line {
  .spacing--medium();
  .padding(bottom, small);
  border-bottom: 1px solid @gray--200;
}

