/**
 * text
 */

p {
  .margin(bottom, small);
  .margin(bottom, layout);
}

strong {
  .font-sans--semibold();
}

small {
  font-size: .8em;
}

.muted {
  opacity: .5;
}

.font--small {
  font-size: 1.3rem;
  line-height: 1.6;
}

.font--medium {
  font-size: 1.5rem;
  line-height: 1.6;
}

.font--big {
  font-size: 1.7rem;
  line-height: 1.6;
}

// code

code {
  margin: -0.1em 0.1em;
  padding: 0.1em .3em;
  background: @gray--200 !important;
  .font-monospace();
  font-size: .9em;
  color: @text;
}

pre {
  overflow-x: hidden !important;
  overflow-y: auto !important;
  .overflow-style(@mode: inner);
  .margin(y, medium) !important;
  .margin(y, layout) !important;
  .padding(all, medium) !important;
  width: 100%;
  border-radius: .5rem;
  border-width: 0;
  border-style: solid;
  background: @gray--200 !important;
  line-height: 0 !important;
  code {
    width: 100%;
    .font-monospace();
    font-size: 12px !important;
    line-height: 1.6 !important;
    white-space: pre-wrap !important;
  }
}

// text--inverse

.text--inverse {
  color: @text--inverse;
  h1, h2, h3, h4, h5, h6, .header {
    color: inherit;
  }
}

/**
 * ul ol
 */

ul, ol {
  .margin(top, none);
  .margin(bottom, small);
  padding-left: 2rem;
  > li {
    .margin(bottom, small);
  }
  > ul, > ol {
    .margin(y, small);
  }
}

ul {
  list-style: disc;
  ul {
    list-style: circle;
  }
}

ol {
  list-style-type: decimal;
  ol {
    list-style-type: lower-alpha;
  }
}

