// .list-reset
// use this mixin to reset styles on xtend ul.list ul.slides_inner etc..

.list-reset() {
  cursor: default;
  list-style: none;
  margin: 0;
  padding: 0;
  > li {
    margin: 0;
    padding: 0;
    background: none;
  }
}

/**
 * dl
 */

dl {
  .margin(bottom, small);
  .margin(bottom, layout);
  .dl--block();
  line-height: 1.6;
  dt {
    .font-sans--semibold();
  }
  dd {
    .font-sans--regular();
  }
}

// .dl--block

.dl--block {
  dt {
    .padding(top, small);
  }
  dd {
    .padding(top, tiny);
  }
}

// .dl--line

.dl--line {
  dt {
    .padding(top, tiny);
  }
  dd {
    .padding(left, small);
    .padding(top, tiny);
  }
}

// .dl--horizontal

.dl--horizontal {
  text-align: left;
  dt {
    .padding(top, tiny);
    .padding(right, small);
    float: left;
    clear: left;
    width: 80px;
  }
  dd {
    .padding(left, small);
    .padding(top, tiny);
    margin-left: 80px;
  }
}
