/**
 * @generate-class--util
 */

// various

.reset-text() {
  font-family: inherit;
  font-style: inherit;
  font-weight: inherit;
  letter-spacing: inherit;
  text-transform: inherit;
  font-size: inherit;
  color: inherit;
  line-height: inherit;
}

.pointer-events--none() {
  pointer-events: none;
}

.links--none() {
  a&, a {
    pointer-events: none;
  }
}

.text-capitalize() {
  display: inline-block;
  text-transform: lowercase;
  &:first-letter {
    text-transform: uppercase;
  }
}

.overflow-style(@mode: none) {
  -webkit-overflow-scrolling: touch;
}

.design-setup() {
  content: '';
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

.clearfix() {
  &:before, &:after {
    content: ' ';
    display: table;
  }
  &:after {
    clear: both;
  }
}

.user-select--none() {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.user-select--text() {
  -webkit-user-select: text;
  -moz-user-select: text;
  -ms-user-select: text;
  user-select: text;
}

.accelerate() {
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-perspective: 1000;
  perspective: 1000;
  transform: translateZ(0);
}

.sr-only() {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  padding: 0;
  width: 1px;
  height: 1px;
  border: 0;
  clip: rect(0, 0, 0, 0);
}

// toggle

.toggle--block() {
  display: none;
  &.active, &.out {
    display: block;
  }
}

.toggle--flex() {
  display: none;
  &.active, &.out {
    display: flex;
  }
}

.toggle--inline-flex() {
  display: none;
  &.active, &.out {
    display: inline-flex;
  }
}

.toggle--inline() {
  display: none;
  &.active, &.out {
    display: inline;
  }
}

.toggle--inline-block() {
  display: none;
  &.active, &.out {
    display: inline-block;
  }
}

.toggle--none() {
  &.active, &.out {
    display: none;
  }
}

.toggle--visible() {
  visibility: hidden;
  opacity: 0;
  &.active, &.out {
    visibility: visible;
    opacity: 1;
  }
}

.toggle--hidden() {
  &.active, &.out {
    visibility: hidden;
    opacity: 0;
  }
}

// animation

.transition-none() {
  transition: initial;
  transition-delay: initial;
}

.animation-none() {
  animation: initial;
  animation-delay: initial;
}

.trans-anim-none() {
  .transition-none();
  .animation-none();
}

.transform-none() {
  transform: initial;
}

.duration-none() {
  transition-duration: initial;
  transition-delay: initial;
  animation-duration: initial;
  animation-delay: initial;
}

// responsive

.responsive(@breakpoint: none) when (@breakpoint = none) {
  max-width: 100%;
  flex-shrink: 0; // fix ie11 images inside flex
  /*
  // fix ie11 bug responsive
  min-height: 2px;
  // fix chrome bug responsive
  .align-self(center);
  */
}

.responsive(@breakpoint: none) when (default()) {
  @query-min: 'min-@{breakpoint}';
  @media @@query-min {
    max-width: 100%;
    flex-shrink: 0; // fix ie11 images inside flex
  }
}

.responsive-y(@breakpoint: none) when (@breakpoint = none) {
  max-height: 100%;
  flex-shrink: 0; // fix ie11 images inside flex
}

.responsive-y(@breakpoint: none) when (default()) {
  @query-min: 'min-@{breakpoint}';
  @media @@query-min {
    max-height: 100%;
    flex-shrink: 0; // fix ie11 images inside flex
  }
}

